.search__results {
    margin: -10px 10px 10px 10px;
    .ant-row {
        height: 90%;
    }
    .ant-col {
        height: 100%;
    }
    .search-result-widget-container {
        height: 98%;
        overflow: auto;
        margin-top: 15px;
        .ant-card {
            margin: 5px 5px 10px 5px;
            .ant-card-body {
                padding: 12px;
            }
            .each-widget {
                display: flex;
                gap: 10px;
                .right-section {
                    flex-grow: 1;
                }
            }
        }
        .each-widget {
            display: flex;
            gap: 10px;
            margin-bottom: 15px;
            .right-section {
                width: 75%;
                h3 {
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
        .tags-section {
            display: flex;
            flex-wrap: wrap;
            gap: 5px 0;
        }
        .footer-section {
            margin: 15px 0 0 0;
            display: flex;
            flex-wrap: wrap;
            gap: 15px;
            .numbers-applied {
                position: absolute;
                right: 10px;
                bottom: 5px;
            }
        }
    }
    @media only screen and (max-width: 600px) {
        margin: 0 5px 5px 0;
        .recruiter-search-details {
            margin: 20px 0 0 0px;
        }
        .search-result-widget-container {
            .ant-card {
                margin: 5px;
            }
        }
    }
    @media only screen and (min-width: 600px) {
        margin: 0 5px 5px 0;
        .recruiter-search-details {
            margin: 20px 0 0 0px;
        }
        .search-result-widget-container {
            .ant-card {
                margin: 5px;
            }
        }
    }
    @media only screen and (min-width: 768px) {
        margin: 10px;
        .recruiter-search-details {
            margin: 20px 0 0 10px;
        }
        .search-result-widget-container {
            .ant-card {
                margin: 5px 5px 10px 5px;
            }
        }
    }
}